'use client';

import React from 'react';
import { LineChart, Line, ResponsiveContainer } from 'recharts';

const testData = [
    { x: 1, y: 30 },
    { x: 2, y: 40 },
    { x: 3, y: 35 }
];

export const RechartsTest = () => {
    console.log('RechartsTest rendering');
    console.log('Recharts imported:', { LineChart, Line, ResponsiveContainer });

    return (
        <div style={{ width: '100%', height: '200px', background: 'red', border: '3px solid yellow' }}>
            <p style={{ color: 'white' }}>RED BOX = Component loaded</p>
            <ResponsiveContainer width="100%" height="100%">
                <LineChart data={testData}>
                    <Line type="monotone" dataKey="y" stroke="#8884d8" strokeWidth={4} />
                </LineChart>
            </ResponsiveContainer>
        </div>
    );
};
